import React from 'react';
import TecApplePayButton from './tecApplePay';

import { useTeInstance } from '../teConnectContext';

/* 

type ApplePayNetworks = [
    'amex',
    'cartesBancaires',
    'chinaUnionPay',
    'discover',
    'eftpos',
    'electron',
    'elo',
    'interac',
    'jcb',
    'mada',
    'maestro',
    'masterCard',
    'privateLabel',
    'visa',
    'vPay'
];

type ApplePayMerchantCapability = [
    'supports3DS', (required),
    'supportsEMV', (Include this value only if you support China Union Pay transactions),
    'supportsCredit',
    'supportsDebit'
]

type ApplePayShippingMethods = {    
    "label": "Free Shipping",
    "detail": "Arrives in 5 to 7 days",
    "amount": "0.00",
    "identifier": "FreeShip"
}


type ApplePayLineItem = {
    "label": "Free Shipping",
    "amount": "0.00",
    "type": "final" //final 'pending'
}

type ApplePayPaymentContact = {
    phoneNumber: string,
    emailAddress: string,
    givenName: string,
    familyName: string,
    phoneticGivenName: string,
    phoneticFamilyName: string,
    addressLines: Array<string>,
    subLocality: string,
    locality: string,
    postalCode: string,
    subAdministrativeArea: string,
    administrativeArea: string,
    country: string,
    countryCode: string,
};

type ApplePayContactField = [
    "email", "name", "phone", "postalAddress", "phoneticName"
]

type ApplePayPaymentRequest = {
    total, //required - ApplePayLineItem
    lineItems, //optional - Array<ApplePayLineItem> - ex: subtotal, shipping, estimated tax, tip, etc. "total" - should still reflect sum of all.
    currencyCode, //required. Three char currency code. ex: "USD"
    shippingType, //optional - ['shipping', 'delivery', 'storePickup', 'servicePickup']
    shippingMethods, //optional - Array<ApplePayShippingMethods>
    merchantCapabilities, //required - one or many of ApplePayMerchantCapability options. If none or both credit and debit - both will be accepted.
    supportedNetworks, //required - one or many of ApplePayNetworks.
    countryCode, //required Two char country code. ex: "US"
    requiredBillingContactFields, //optional - one or many of ApplePayContactFields. Will require inputs from cardholder if specified.
    billingContact, //optional. ApplePayPaymentContact obj. Can set fields if address is on file - and user can update if needed on display.
    requiredShippingContactFields, //optional - same as requiredBillingContactFields.
    ApplePayPaymentContact shippingContact, //optional - same as billingContact.
    applicationData, //optional - Base64 string used to contain application-specific data (TODO: maybe leverage this for TEConnect).
    supportedCountries, //optional Array<TwoCharCountryCodes> used to limit cards to specific countries. Does not affect currency or country code supplied.
    supportsCouponCode, //optional - beta property. Toggles "coupon code" input field in payment sheet. Limited support.
    couponCode, //optional - beta property. Can pre-populate - or empty string if user input needed. Limited support.
    shippingContactEditingMode //optional - beta property - oneOf(['enabled', 'storePickup']). enabled allows user to edit shipping info - storePickup prevents that.
};

*/

export const useTecPaymentsRequest = _ => {
    const { paymentsRequestInterface } = useTeInstance();

    return paymentsRequestInterface;
}

export const TecPaymentRequestButtons = ({ paymentRequestInterface }) =>
    <TecApplePayButton prInterface={ paymentRequestInterface } />;
