import {
    teConnectKeyNames, 
    prInterfaceProps,
    NO_INSTANCE, 
    INCOMPLETE_INSTANCE,
    MISSING_INTERFACE,
    INTERFACE_NOT_INSTANTIATED,
    PR_INTERFACE_MALFORMED
} from '../consts'

export const validateTeConnect = teConnectInstance => {
    if (!teConnectInstance)
        throw new Error(NO_INSTANCE);

    if (typeof(teConnectInstance.requestClient) !== 'object')
        throw new Error(INCOMPLETE_INSTANCE);
    
    const { requestClient } = teConnectInstance;

    if (typeof(requestClient.hasOwnProperty) !== 'function')
        throw new Error(INCOMPLETE_INSTANCE);

    for(const keyName of teConnectKeyNames) {
        if (!requestClient.hasOwnProperty(keyName)) {
            throw new Error(INCOMPLETE_INSTANCE);
        }
    }

    return teConnectInstance;
}

export const validatePrInterface = prInterface => {
    console.log('inside interface validation', prInterface);

    if (!prInterface)
        throw new Error(MISSING_INTERFACE);

    if (typeof(prInterface) === 'function')
        throw new Error(INTERFACE_NOT_INSTANTIATED);

    for (const interfaceProp of prInterfaceProps)
        if (typeof(prInterface[interfaceProp]) !== 'function')
            throw new Error(PR_INTERFACE_MALFORMED)

    const { createPaymentRequestToken: createPaymentBase } = prInterface;

    return ({ createPaymentBase });
}
